import { Component, ViewChild, ElementRef } from "@angular/core";
import { Router } from '@angular/router';
import { LoginService, StateStorageService } from "../../core";
import { UserLoginInfoService } from './../../shared/user-login-info/user-login-info.service';
import { PluginService } from '../../shared/plugin/plugin.service';

@Component({
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.scss"]
})
export class LoginComponent {
  @ViewChild("userid", { read: ElementRef }) userid: ElementRef;
  @ViewChild("password", { read: ElementRef }) password: ElementRef;
  @ViewChild("remember", { read: ElementRef }) remember: ElementRef;

  constructor(private loginService: LoginService,
    private router: Router,
    private stateStorageService: StateStorageService,
    public userLoginService: UserLoginInfoService,
    public pluginService: PluginService) { }

  login(event) {
    event.preventDefault();
    this.loginService.login({
      username: this.userid.nativeElement.value,
      password: this.password.nativeElement.value,
      rememberMe: this.remember.nativeElement.checked
    }).then(response => {

      // previousState was set in the authExpiredInterceptor before being redirected to login modal.
      // since login is succesful, go to stored previousState and clear previousState
      const redirect = this.stateStorageService.getUrl();

      if (redirect) {
        this.stateStorageService.storeUrl(null);
        this.router.navigateByUrl(redirect);
      } else {
        this.pluginService.alertMessage("Login successfully", "success");
        this.router.navigateByUrl('/general-subject');
      }
    }, error => {
      this.pluginService.alertMessage("Username and password doesn't match...! Please try again :(", "error");
    });
  }
}
