import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { Observable } from 'rxjs';

let headers: HttpHeaders = new HttpHeaders();
headers = headers.append('Access-Control-Allow-Origin', '*').append('Access-Control-Allow-Headers', '*');

@Injectable({ providedIn: 'root' })
export class FileManagementService {
    public generateURL = environment.API_URL + 'services/content/api/file/file-upload-url';
    public downloadFile = environment.API_URL + 'services/content/api/file/download-url';
    public deleteFile = environment.API_URL + 'services/content/api/file/delete';
    public imgBase64File = environment.API_URL + 'services/content/api/file/image-upload-url';

    constructor(private http: HttpClient) { }

    //Service call to get a presigned URL from AWS 
    getpresignedurls(fileName: string): Observable<any> {
        return this.http.get<any>(`${this.generateURL}/${fileName}`, { observe: 'response' });
    }

    //Upload a PDF file to a presigned URL from AWS
    uploadPDFFile(fileuploadurl, file: File): Observable<any> {
        const formdata: FormData = new FormData();
        formdata.append('file', file);
        return this.http.put<any>(fileuploadurl, formdata, { observe: 'response' })
    }

    //Service call to get a presigned URL from AWS
    getImagePresignedurls(fileName: string) {
        //return this.http.get<any>(`${this.imgBase64File}/${fileName}`, { observe: 'response' });
        return this.http.get(`${this.imgBase64File}/${fileName}`).toPromise();
    }


    //Upload a image file to a presigned URL from AWS
    uploadImagefileAWSS3(fileuploadurl, base64Data) {
        let binaryBuf = this.base64ToArrayBuffer(base64Data);
        //return this.http.put<any>(fileuploadurl, binaryBuf, { observe: 'response' })
        return this.http.put(fileuploadurl, binaryBuf).toPromise();
    }

    //Upload a video file to a presigned URL from AWS
    uploadVideoFile(fileuploadurl, base64Data): Observable<any> {
        let binaryBuf = this.base64ToArrayBuffer(base64Data);
        return this.http.put<any>(fileuploadurl, binaryBuf, { observe: 'response' })
    }

    //Service call to delete a uploaded file
    deleteUploadedFile(fileName: string): Observable<HttpResponse<any>> {
        return this.http.delete<any>(`${this.deleteFile}/${fileName}`, { observe: 'response' });
    }

    //Base64 to array buffer conversion
    base64ToArrayBuffer(base64) {
        var binary_string = window.atob(base64);
        var len = binary_string.length;
        var bytes = new Uint8Array(len);
        for (var i = 0; i < len; i++) {
            bytes[i] = binary_string.charCodeAt(i);
        }
        return bytes.buffer;
    }

    //Service call to download files
    downloadFiles(fileName) {
        return this.http.get(`${this.downloadFile}/${fileName}`, { observe: 'response' });
    }
}