import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { IUserLoginInfo } from '../model/common/user.login.info.model';
import { PluginService } from '../plugin/plugin.service';
import { AccountService, Principal } from '../../../app/core';
import { IUsers } from '../model/common/users.model';


@Injectable({ providedIn: 'root' })
export class UserLoginInfoService {
    organisationId: number;

    constructor(
        protected http: HttpClient,
        public accountService: AccountService,
        public pluginService: PluginService,
        public principalService: Principal
    ) {
        if (principalService.authenticated === true) {
            this.getLoginInformation();
        }
    }

    getLoginInformation() {
        this.accountService.get()
            .subscribe(
                (res: HttpResponse<IUsers>) => this.assignLoginInfo(res.body),
                (res: HttpErrorResponse) => this.onError(res.message)
            );
    }

    assignLoginInfo(data: IUsers) {
        let login = data.login;
        this.getUserLoginInfo(login);
    }

    protected onError(errorMessage: string) {
        this.pluginService.alertMessage(errorMessage, "error");
    }


    getUserLoginInfo(login) {
        this.accountService.getUserLoginInfo(login)
            .subscribe(
                (res: HttpResponse<IUserLoginInfo>) => this.assignUserLoginInfo(res.body),
                (res: HttpErrorResponse) => this.onError(res.message)
            );
    }

    assignUserLoginInfo(data: IUserLoginInfo) {
        this.organisationId = data.organisationId;
    }
}